<template>
  
  <vs-row vs-justify="center">
    <vs-col type="flex" vs-justify="center" vs-align="center" vs-w="12">
      <vs-card>

        <!--        <div slot="header">-->
        <!--          <div class="vx-row">-->
        <!--            <div class="vx-col sm:w-1/2 w-full" style="text-align: center;">-->
        <!--              <vs-radio class="inline-flex" v-model="gurdian" vs-value="new">New Gurdian</vs-radio>-->
        <!--            </div>-->
        <!--            <div class="vx-col sm:w-1/2 w-full" style="text-align: center;">-->
        <!--              <vs-radio class="inline-flex" color="success" v-model="gurdian" vs-value="old">Old Gurdian</vs-radio>-->
        <!--            </div>-->
        <!--          </div>-->
        <!--        </div>-->

        <div>
          <!-- Applicant information Start -->
          <div class="vx-card">
            <div class="header-border" style="padding-bottom: 0 !important;">
              <h3 class="heading-subtitle"><span>Applicant Information</span></h3>

              <div class="vx-card__collapsible-content vs-con-loading__container">
                <div class="vx-card__body">
                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-select v-model="applicant.class" :disabled="classDisable" name="class"
                                 @change="getQuerySessionData($event)"
                                 class="w-full select-large vs-con-loading__container" id="classOption" type="filled"
                                 label="Class *" v-validate="'required'">

                        <vs-select-item
                          :key="index"
                          :value="item.id"
                          :text="item.name"
                          v-for="(item,index) in classOption"
                          class="w-full"
                        />
                      </vs-select>

                      <span class="text-danger text-sm" v-show="errors.has('class')">{{ errors.first('class') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-select v-model="applicant.shift" class="w-full select-large vs-con-loading__container"
                                 id="shiftOption" type="filled" label="Shift" :disabled="shiftDisable">

                        <vs-select-item
                          :key="index"
                          :value="item.value"
                          :text="item.text"
                          v-for="(item,index) in shiftOption"
                          class="w-full"
                        />
                      </vs-select>
                    </div>
                  </div>

                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-select name="session" class="w-full select-large vs-con-loading__container" id="sessionOption"
                                 type="filled" v-validate="'required'" v-model="applicant.session"
                                 :disabled="sessionDisable" label="Session *">

                        <vs-select-item
                          :key="index"
                          :value="item.id"
                          :text="item.name"
                          v-for="(item,index) in sessionOption"
                          class="w-full"
                        />
                      </vs-select>

                      <span class="text-danger text-sm"
                            v-show="errors.has('session')">{{ errors.first('session') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-select name="campus" v-validate="'required'" v-model="applicant.campus"
                                 class="w-full select-large" label="Campus *">

                        <vs-select-item
                          :key="index"
                          :value="item.id"
                          :text="item.name"
                          v-for="(item,index) in campusOption"
                          class="w-full"
                        />
                      </vs-select>

                      <span class="text-danger text-sm"
                            v-show="errors.has('campus')">{{ errors.first('campus') }}</span>
                    </div>
                  </div>

                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-input class="w-full" label="Applicant First Name *" v-validate="'required'"
                                name="Applicant First Name" v-model="applicant.firstName"/>
                      <span class="text-danger text-sm" v-show="errors.has('Applicant First Name')">{{ errors.first('Applicant First Name') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-input class="w-full" name="Applicant Last Name" v-validate="'required'"
                                label="Applicant Last Name *" v-model="applicant.lastName"/>
                      <span class="text-danger text-sm" v-show="errors.has('Applicant Last Name')">{{ errors.first('Applicant Last Name') }}</span>
                    </div>
                  </div>

                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <label>Date Of Birth *</label>
                      <datepicker name="date of birth" v-validate="'required'" v-model="applicant.dob"></datepicker>
                      <span class="text-danger text-sm" v-show="errors.has('date of birth')">{{ errors.first('date of birth') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-select name="gender" v-validate="'required'" v-model="applicant.gender"
                                 class="w-full select-large" label="Gender *">
                        <vs-select-item
                          :key="index"
                          :value="item.value"
                          :text="item.text"
                          v-for="(item,index) in genderOption"
                          class="w-full"
                        />
                      </vs-select>

                      <span class="text-danger text-sm"
                            v-show="errors.has('gender')">{{ errors.first('gender') }}</span>
                    </div>
                  </div>

                  <div class="vx-row">
                    <div class="vx-col w-full mb-2">
                      <label>Special Request</label>
                         <vs-textarea v-model="applicant.note"/>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- Applicant information End -->

          <!-- Mother information Start -->
          <div class="vx-card">
            <div class="header-border">
              <h3 class="heading-subtitle">
                <span>Mother Information</span>
              </h3>
              <div class="vx-card__collapsible-content vs-con-loading__container">
                <div class="vx-card__body">
                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-input class="w-full" name="Mother First Name" v-validate="'required'"
                                label="Mother's First Name *" v-model="applicant.motherFirstName"/>
                      <span class="text-danger text-sm" v-show="errors.has('Mother First Name')">{{ errors.first('Mother First Name') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-input name="Mother Last Name" v-validate="'required'" class="w-full"
                                label="Mother's Last Name *" v-model="applicant.motherLastName"/>
                      <span class="text-danger text-sm" v-show="errors.has('Mother Last Name')">{{ errors.first('Mother Last Name') }}</span>

                    </div>
                  </div>

                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-input class="w-full" name="Mother Mobile" v-validate="'required|digits:11'"
                                label="Mother's Mobile *" v-model="applicant.motherMobile"/>
                      <span class="text-danger text-sm" v-show="errors.has('Mother Mobile')">{{ errors.first('Mother Mobile') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-input class="w-full" label="Mother's Email *" name="motherEmail" v-validate="'required||email'"
                                v-model="applicant.motherEmail"/>
                      <span class="text-danger text-sm" v-show="errors.has('motherEmail')">{{ errors.first('motherEmail') }}</span>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- Mother information End -->

          <!-- Father information Start -->
          <div class="vx-card">
            <div class="header-border">
              <h3 class="heading-subtitle">
                <span>Father Information</span>
              </h3>
              <div class="vx-card__collapsible-content vs-con-loading__container">
                <div class="vx-card__body">
                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-input class="w-full" name="Father First Name" v-validate="'required'"
                                label="Father's First Name *" v-model="applicant.fatherFirstName"/>
                      <span class="text-danger text-sm" v-show="errors.has('Father First Name')">{{ errors.first('Father First Name') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-input name="Father Last Name" v-validate="'required'" class="w-full"
                                label="Father's Last Name *" v-model="applicant.fatherLastName"/>
                      <span class="text-danger text-sm" v-show="errors.has('Father Last Name')">{{ errors.first('Father Last Name') }}</span>

                    </div>
                  </div>

                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-input class="w-full" name="Father Mobile" v-validate="'required|digits:11'"
                                label="Father's Mobile *" v-model="applicant.fatherMobile"/>
                      <span class="text-danger text-sm" v-show="errors.has('Father Mobile')">{{ errors.first('Father Mobile') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-input class="w-full" label="Father Email" name="fatherEmail" v-validate="'email'"
                                v-model="applicant.fatherEmail"/>
                      <span class="text-danger text-sm" v-show="errors.has('fatherEmail')">{{ errors.first('fatherEmail') }}</span>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- Father information End -->

          <!-- Address information Start -->
          <div class="vx-card">
            <div class="header-border">
              <h3 class="heading-subtitle">
                <span>Address</span>
              </h3>
              <div class="vx-card__collapsible-content vs-con-loading__container">
                <div class="vx-card__body">
                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-input class="w-full" label="House No" v-model="applicant.houseNo"/>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-input class="w-full" label="Road No" v-model="applicant.roadNo"/>

                    </div>
                  </div>

                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-select class="w-full select-large vs-con-loading__container" id="divisionOption" type="filled"
                                 name="division" v-validate="'required'" v-model="applicant.division"
                                 @change="getDistrict" label="Division *">

                        <vs-select-item
                          :key="index"
                          :value="item.id"
                          :text="item.name"
                          v-for="(item,index) in divisionOption"
                          class="w-full"
                        />
                      </vs-select>

                      <span class="text-danger text-sm"
                            v-show="errors.has('division')">{{ errors.first('division') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <vs-select name="district" class="w-full select-large vs-con-loading__container"
                                 id="districtOption"
                                 type="filled" v-validate="'required'" v-model="applicant.district" @change="getThana"
                                 :disabled="districtDisable" label="District *">

                        <vs-select-item
                          :key="index"
                          :value="item.id"
                          :text="item.name"
                          v-for="(item,index) in districtOption"
                          class="w-full"
                        />
                      </vs-select>

                      <span class="text-danger text-sm"
                            v-show="errors.has('district')">{{ errors.first('district') }}</span>

                    </div>
                  </div>
                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-select name="thana" class="w-full select-large vs-con-loading__container" id="thanaOption"
                                 type="filled" v-validate="'required'" v-model="applicant.thana" @change="getPostCode"
                                 :disabled="thanaDisable" label="Thana *">

                        <vs-select-item
                          :key="index"
                          :value="item.id"
                          :text="item.name"
                          v-for="(item,index) in thanaOption"
                          class="w-full"
                        />
                      </vs-select>
                      <span
                        class="text-danger text-sm"
                        v-show="errors.has('thana')"
                      >{{ errors.first('thana') }}</span>
                    </div>
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <vs-select name="postCode" class="w-full select-large vs-con-loading__container"
                                 id="postCodeOption"
                                 type="filled" v-validate="'required'" v-model="applicant.postCode"
                                 :disabled="postCodeDisable" label="Post Code *">

                        <vs-select-item
                          :key="index"
                          :value="item.id"
                          :text="item.name"
                          v-for="(item,index) in postCodeOption"
                          class="w-full"
                        />
                      </vs-select>
                      
                      <span class="text-danger text-sm"
                            v-show="errors.has('postCode')">{{ errors.first('postCode') }}</span>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- Address information End -->

          <!-- Document CheckList information Start -->
          <div class="vx-card">
            <div class="header-border">
              <h3 class="heading-subtitle">
                <span>Document CheckList</span>
              </h3>
              <div class="vx-card__collapsible-content vs-con-loading__container">
                <div class="vx-card__body">
                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <label
                        style="font-size: 12px !important;"
                      >ID Proof Of The Child(NID/Passport/Birthcertificate)</label>
                      <input
                        style="border: 1px solid #eee;padding: 10px;border-radius: 5px;"
                        @change="onImageChange"
                        data-vv-as="field"
                        name="id_proof_of_child"
                        type="file"
                        class="w-full"
                      />
                      <br />
                      <span
                        class="text-danger text-sm"
                        v-show="errors.has('id_proof_of_child')"
                      >{{ errors.first('id_proof_of_child') }}</span>

                    </div>
                  </div>
                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">
                      <label style="font-size: 12px !important;">ID Proof Of Mother(NID/Passport)</label>
                      <input
                        style="border: 1px solid #eee;padding: 10px;border-radius: 5px;"
                        @change="onImageChange"
                        data-vv-as="field"
                        name="id_proof_of_mother"
                        type="file"
                        class="w-full"
                      />
                      <br />
                      <span
                        class="text-danger text-sm"
                        v-show="errors.has('id_proof_of_mother')"
                      >{{ errors.first('id_proof_of_mother') }}</span>
                    </div>
                  </div>
                  <div class="vx-row">
                    <div class="vx-col sm:w-1/2 w-full mb-2">

                      <label
                        style="font-size: 12px !important;"
                      >Recent passport size photograph of child</label>
                      <input
                        style="border: 1px solid #eee;padding: 10px;border-radius: 5px;"
                        @change="onImageChange"
                        data-vv-as="field"
                        name="child_photo_passport_size"
                        type="file"
                        class="w-full"
                      />
                      <br />
                      <span
                        class="text-danger text-sm"
                        v-show="errors.has('child_photo_passport_size')"
                      >{{ errors.first('child_photo_passport_size') }}</span>

                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <!-- Document CheckList information End -->
          <vs-prompt
            color="primary"
            title="Applicant Verification"
            @cancel="emailCodeValue=''"
            @close="emailCodeValue=''"
            @accept="acceptEmailCode"
            :is-valid="validCodeField"
            :active.sync="emailCodePrompt">
            <div class="con-exemple-prompt">
            Please enter the verification code sent to your email
              <vs-input type="number" maxLength="4" min=0 placeholder="Applicant ID" v-model="emailCodeValue"
              oninput="javascript: if (this.value.length > this.maxLength) this.value = this.value.slice(0, this.maxLength);" />

              <vs-alert :active="!validCodeField" color="danger" icon="new_releases" >
                Applicant ID does not match!
              </vs-alert>
            </div>
          </vs-prompt>

          <div class="flex flex-wrap items-center justify-end">
            <vs-button color="warning" type="border" class="mb-2 mr-3" @click="resetForm()">Reset</vs-button>
            <!-- <vs-button @click="editMode ? applicantFormUpdate() : applicantFormSubmit()" class="mr-3 mb-2">{{ editMode ?
              'Update':'Save'}} & Next
            </vs-button> -->
            <vs-button @click="editMode ? applicantFormUpdate() : openConfirm()" class="mr-3 mb-2">
              Save
            </vs-button>

            <!-- <vs-button color="success" @click="SubmitWithPay()" class="mr-3 mb-2">Pay Now</vs-button> -->
          </div>
        </div>
      </vs-card>
    </vs-col>
  </vs-row>
</template>

<script>
  import Datepicker from 'vuejs-datepicker';
  // import { Validator } from 'vee-validate';
  //   const dict = {
  //     custom: {
  //       Passport: {
  //         ext: 'Please enter PDF File',
  //         size: "Please Uploads File Under MB"
  //       },
  //       birth_certificate: {
  //         ext: 'Please enter PDF File',
  //         size: "Please Uploads File Under MB"
  //       },
  //     }
  //   };

  // register custom messages
  // Validator.localize('en', dict);
  export default {
    components: {
      Datepicker
    },

    mounted() {
      this.$root.$on('searchApplicant', (dataGet) => {
        this.applicantSearch(dataGet)
      })
      this.$vs.loading({
        color: 'rgba(var(--vs-primary), 1)',
        container: '#classOption',
        scale: 0.5
      })
      this.getGlobalInfo();
      this.getDivision();
    },

    data() {
      return {
      	globalLoading: document.getElementById('loading-bg'),
        emailCodePrompt:false,
        emailCodeValue: '',
        editMode: false,
        gurdian: 'new',
        classDisable: true,
        shiftDisable: true,
        sessionDisable: true,
        districtDisable: true,
        thanaDisable: true,
        postCodeDisable: true,
        applicantId: null,
        applicant: {
          class: null,
          shift: null,
          session: null,
          campus: null,
          firstName: null,
          lastName: null,
          dob: null,
          gender: null,
          note: null,
          motherFirstName: null,
          motherLastName: null,
          motherMobile: null,
          motherEmail: null,
          fatherFirstName: null,
          fatherLastName: null,
          fatherMobile: null,
          fatherEmail: null,
          houseNo: null,
          roadNo: null,
          division: null,
          district: null,
          thana: null,
          postCode: null,
          birthCertificate: null,
          passport: null,
          idProofOfChild: null,
          idProofOfMother: null,
          photoOfChild: null,
	     },
        studentInfo: [
          {studentId: 10000124004, name: "Sabber", class: 'six', section: 'jupitop', roll: 10},
          {studentId: 10000343434, name: "hossain", class: 'five', section: 'golap', roll: 11},
        ],
        classOption: [
          {name: "Select Class", id: null},
        ],
        shiftOption: [
          {text: "Select Shift", value: null},
          {text: "Morning", value: "1"},
          {text: "Day", value: "2"},
        ],
        sessionOption: [
          {name: "Select Session", id: null},
        ],
        campusOption: [
          {name: "Select Branch", id: null},
        ],
        genderOption: [
          {text: "Select Gender", value: null},
          {text: "Male", value: "1"},
          {text: "Female", value: "2"},
        ],
        divisionOption: [
          {name: "Select Division", id: null},
        ],
        districtOption: [
          {name: "Select District", id: null},
        ],
        thanaOption: [
          {name: "Select Thana", id: null},
        ],
        postCodeOption: [
          {name: "Select Post Code", id: null},
        ],
      }
    },

    computed:{
      validCodeField(){
        return (this.emailCodeValue.length > 0 && this.motherEmail && this.applicantId == this.emailCodeValue)
      }
    },
      
    methods: {
      acceptEmailCode(){
        axios.post(apiUrl + 'admission/verifyEmail', {applicantId: this.applicantId, instituteID: this.$route.params.instituteID})
        .then(response => {
          if (response.data.status) {
            this.$vs.notify({
              color: 'success',
              title: 'Success',
              text: response.data.message,
              position: 'top-right'
            })
            this.$vs.resetForm();
          } else {
            this.$vs.notify({color: 'danger', title: 'Error', text: response.data.message, position: 'top-right'})
          }
        })
      },
      closeEmailCodePrompt(){
        this.$vs.notify({
          color:'danger',
          title:'Closed',
          text:'You close a dialog!'
        })
      },
      applicantSearch(applicantId) {
        if (applicantId) {
        	this.$vs.loading({
		        color: 'rgba(var(--vs-primary), 1)',
		        container: '#loading-bg',
		        scale: 0.5
		      })
        	this.globalLoading.style.display='block'
        	this.globalLoading.style.position='relative'
          	axios.get(apiUrl + 'admission/applicantSearch/' + applicantId)
            .then(response => {
              if (response.data.status) {
                this.editMode = true
                this.$vs.notify({
                  color: 'success',
                  title: 'Success',
                  text: response.data.message,
                  position: 'top-right'
                })
                let applicantInfo = response.data.data
                this.applicantId = applicantInfo.id
                this.applicant.class = applicantInfo.class_id
                this.applicant.shift = applicantInfo.shift_id
                this.applicant.session = applicantInfo.academic_year_id
                this.applicant.campus = applicantInfo.branch_id
                this.applicant.campus = applicantInfo.branch_id
                this.applicant.campus = applicantInfo.branch_id
                this.applicant.firstName = applicantInfo.first_name
                this.applicant.lastName = applicantInfo.last_name
                this.applicant.dob = applicantInfo.dob
                this.applicant.gender = applicantInfo.gender
                this.applicant.previousSchool = applicantInfo.previous_school
                this.applicant.note = applicantInfo.note
                this.applicant.motherFirstName = applicantInfo.guardian_first_name
                this.applicant.motherLastName = applicantInfo.guardian_last_name
                this.applicant.motherMobile = applicantInfo.guardian_contact
                this.applicant.motherEmail = applicantInfo.guardian_email
                this.applicant.fatherFirstName = applicantInfo.branch_id
                this.applicant.fatherLastName = applicantInfo.guardian_first_name
                this.applicant.fatherMobile = applicantInfo.guardian_contact
                this.applicant.fatherEmail = applicantInfo.guardian_email
                this.applicant.houseNo = applicantInfo.address.present_house_no
                this.applicant.roadNo = applicantInfo.address.present_road_no
                this.applicant.division = applicantInfo.address.present_division_id
                this.applicant.district = applicantInfo.address.present_district_id
                this.applicant.thana = applicantInfo.address.present_thana_id
                this.applicant.postCode = applicantInfo.address.present_post_code
                this.globalLoading.style.display ='none'
	        	this.globalLoading.style.position ='absolute'
                this.$vs.loading.close("#loading-bg")
	        	
              } else {
              	this.globalLoading.style.display ='none'
	        	this.globalLoading.style.position ='absolute'
                this.$vs.loading.close("#loading-bg")
                this.$vs.notify({color: 'danger', title: 'Error', text: response.data.message, position: 'top-right'})
              }
            })
            .catch(e => {
              console.log(e)
            })
        }
      },

      //Get Global info
      getGlobalInfo() {
        axios.get(apiUrl + 'admission/getInfo/' + this.$route.params.instituteID)
          .then(response => {
            if (response.data.status) {
              localStorage.setItem('institutionName', response.data.data.institutioinInfo.name);
              this.classDisable = false
              this.$vs.loading.close("#classOption > .con-vs-loading")
              this.classOption = this.classOption.concat(response.data.data.classInfo)
              this.campusOption = this.campusOption.concat(response.data.data.campusInfo)
              // this.$vs.notify({
              //   color: 'success',
              //   title: 'Data Loaded',
              //   text: response.data.message,
              //   position: 'top-right'
              // })
            }else{
              this.$vs.notify({color:'danger',title:'Error',text:response.data.message,position:'top-right'})
            }

          })
          .catch(e => {
            this.$vs.loading.close("#classOption > .con-vs-loading")
            console.log(e)
          })
      },

      //Get Class wise Query Session Data
      getQuerySessionData(event) {
        var index = this.classOption.findIndex(item => {
          return item.id == event
        })
        if (this.classOption[index].has_shift > 0) {
          this.shiftDisable = false
        } else {
          this.shiftDisable = true
        }

        this.$vs.loading({
          color: 'rgba(var(--vs-primary), 1)',
          container: '#sessionOption',
          scale: 0.5
        })
        axios.get(apiUrl + 'admission/getQuerySession/' + this.applicant.class + '/' + this.$route.params.instituteID)
          .then(response => {
          	if(response.data.status){
          		this.sessionOption = response.data.data.QuerySessionData
	            this.sessionDisable = false
          	}else{
          		this.$vs.notify({
                color: 'warning',
                title: 'Error',
                text: response.data.message,
                position: 'top-right'
              })
          	}
            this.$vs.loading.close("#sessionOption > .con-vs-loading")
            // this.classOption = response.data.data.classInfo
          })
          .catch(e => {
            console.log(e)
          })

      },

      //get Division
      getDivision() {
        axios.get(apiUrl + 'getDivision')
          .then(response => {
            this.divisionOption = this.divisionOption.concat(response.data.data)
          })
          .catch(e => {
            console.log(e)
          })
      },

      //Get District by division id

      getDistrict(){
        // this.districtDisable = true
        // this.thanaDisable = true
        // this.postCodeDisable = true
        
        if(this.applicant.division){
          this.$vs.loading({
            color: 'rgba(var(--vs-primary), 1)',
            container: '#districtOption',
            scale: 0.5
          })
          axios.get(apiUrl + 'getDistrict/' + this.applicant.division)
            .then(response => {
              this.districtOption = this.districtOption.concat(response.data.data)
              this.districtDisable = false
              this.$vs.loading.close("#districtOption > .con-vs-loading")
            })
            .catch(e => {
              console.log(e)
            })
        }

      },

      //Get Thana by District Id
      getThana(){
        // this.thanaDisable = true
        // this.postCodeDisable = true
        
        if(this.applicant.district){
          this.$vs.loading({
            color: 'rgba(var(--vs-primary), 1)',
            container: '#thanaOption',
            scale: 0.5
          })
          axios.get(apiUrl + 'getThana/' + this.applicant.district)
            .then(response => {
              this.thanaOption = this.thanaOption.concat(response.data.data)
              this.thanaDisable = false
              this.$vs.loading.close("#thanaOption > .con-vs-loading")
            })
            .catch(e => {
              console.log(e)
            })
        }
      },

      //Get Postcode by thana Id
      
      getPostCode(){
        // this.postCodeDisable = true
        if(this.applicant.thana){
        this.$vs.loading({
          color: 'rgba(var(--vs-primary), 1)',
          container: '#postCodeOption',
          scale: 0.5
        })
        axios.get(apiUrl+'getPostCode/'+this.applicant.thana)
          .then(response => {
            this.postCodeOption = this.postCodeOption.concat(response.data.data)
            this.postCodeDisable = false
            this.$vs.loading.close("#postCodeOption > .con-vs-loading")
          })
          .catch(e => {
            console.log(e)
          })
        }
      },

      //applicant form submit
      applicantFormSubmit() {
        this.$validator.validateAll().then(result => {
          if (result) {
            // axios.post(apiUrl + 'admission/newEnquirey', this.applicant)
            axios.post(apiUrl + 'admission/saveApplication', this.applicant)
              .then(response => {
                if (response.data.status) {
                  this.applicantId = response.data.data.id;
                  this.applicant.motherEmail = response.data.data.guardian_email;
                  this.instituteID = this.$route.params.instituteID;
                  this.emailCodePrompt = true;
                  // this.$vs.notify({
                  //   color: 'success',
                  //   title: 'Success',
                  //   text: response.data.message,
                  //   position: 'top-right'
                  // })
                } else {
                  this.$vs.notify({color: 'danger', title: 'Error', text: response.data.message, position: 'top-right'})
                }
              })
              .catch(e => {
                console.log(e)
              })
          } else {
            this.$vs.notify({color: 'danger', title: 'Error', text: "Something went worng", position: 'top-right'})
          }
        })
      },

      //applicant update
      applicantFormUpdate() {
        this.$validator.validateAll().then(result => {
          if (result) {

            axios.put(apiUrl+'admission/updateEnquirey/'+this.applicantId, this.applicant)
            .then(response => {
              if(response.data.status){
                this.$vs.notify({color:'success',title:'Success',text:response.data.message,position:'top-right'})
              }else{
                this.$vs.notify({color:'danger',title:'Error',text:response.data.message,position:'top-right'})
              }
                
            })
	        .catch(e => {
	           console.log(e)
	        })
          } else {
            this.$vs.notify({color: 'danger', title: 'Error', text: "Something went worng", position: 'top-right'})
          }
        })
      },

      //reset form
      resetForm() {
        this.shiftDisable = true
        this.sessionDisable = true
        this.districtDisable = true
        this.thanaDisable = true
        this.postCodeDisable = true
        this.applicant.shift = null;
        this.applicant.class = null;
        this.applicant.session = null;
        this.applicant.campus = null;
        this.applicant.firstName = null;
        this.applicant.lastName = null;
        this.applicant.dob = null;
        this.applicant.gender = null;
        this.applicant.previousSchool = null;
        this.applicant.motherFirstName = null;
        this.applicant.motherLastName = null;
        this.applicant.motherMobile = null;
        this.applicant.motherEmail = null;
        this.applicant.fatherFirstName = null;
        this.applicant.fatherLastName = null;
        this.applicant.fatherMobile = null;
        this.applicant.fatherEmail = null;
        this.applicant.note = null;
        this.applicant.division = null;
        this.applicant.houseNo = null;
        this.applicant.roadNo = null;
        this.applicant.idProofOfChild = null;
        this.applicant.idProofOfMother = null;
        this.applicant.photoOfChild = null;
        this.$validator.reset();
      },

      onImageChange(e) {
        let files = e.target.files || e.dataTransfer.files;
        let fileName = e.target.name.replace(/_/g, '-'); // replace all '_' with '-' in the name

        if (!files.length) return;
        
        let fileExtension = files[0].type.split("/").pop();
        let fileSize = (files[0].size / 1024); // in KB
        
        this.createImage(files[0], fileName);
        
        axios
          .post(apiUrl + "checkFileValidity", {
            "doc_type" : fileName,
            "size" : fileSize,
            "extension" : fileExtension
          })
          .then(response => {
            if (!response.data.status) {
              this.$vs.notify({
                color: "danger",
                title: "Error",
                text: response.data.message,
                position: "top-right"
              });
            }
          })
          .catch(e => {
            console.log(e);
          });
      },

      createImage(file, fileName) {
        let reader = new FileReader();
        reader.onload = e => {
          if (fileName == "id-proof-of-child") {
            this.applicant.idProofOfChild = e.target.result;
          } else if (fileName == "id-proof-of-mother") {
            this.applicant.idProofOfMother = e.target.result;
          } else if (fileName == "child-photo-passport-size") {
            this.applicant.photoOfChild = e.target.result;
          } else {
            this.$vs.notify({
              color: "danger",
              title: "Invalid File Type",
              text: "please select Valid file type",
              position: "top-right"
            });
          }
        };
        reader.readAsDataURL(file);
        // let test = reader.readAsDataURL(file);
      },

      openConfirm(){
        this.$vs.dialog({
          type:'confirm',
          color: 'primary',
          title: `Confirm`,
          // text: `An email will be sent to ${this.applicant.motherEmail} comprising of your applicant ID and passcode. Please use this applicant ID as reference for payment of the application form through bKash.`,
          text: `An email will be sent to ${this.applicant.motherEmail} comprising of your applicant ID and passcode.
          Please use this applicant ID as reference for payment of the application form through bKash.`,
          accept:this.applicantFormSubmit
        })
        },
        // acceptAlert(color){
        //   this.$vs.notify({
        //     color:'primary',
        //     title:'Deleted image',
        //     text:'The selected image was successfully deleted'
        //   })
        // },
      }
};
</script>

